import React from "react";
import Project from "./Project";
import { PROJECTS_INFORMATION } from "./ProjectsInformation";
import "./ProjectList.css";

const ProjectList = (props) => {
  return (
    <div className="center column">
      <div className="column project-list center">
        <h1 className="project-title">Projects</h1>
        <ul>
          {PROJECTS_INFORMATION.map((project) => {
            return (
              <Project
                title={project.title}
                technologies={project.technologies}
                description={project.description}
                url={project.url}
                imageUrl={project.imageUrl}
                whatilearned={project.whatilearned}
                wideimage={project.wideimage}
                githubLink={project.githubLink}
                nogit={project.nogit}
                showproject={project.showproject}
              />
            );
          })}
        </ul>
      </div>
    </div>
  );
};

export default ProjectList;
